@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.bienvenida {
    @include m.bgImage( "../img/fondo-bienvenida.webp", v.$primarioOpacity);
    display: flex;
    position: relative;
    top: 5rem;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 70rem;
    &__principal-box,
    &__principal-box-activo {
        background-color: lighten(v.$primario, 3);
        margin-top: 14rem;
        width: 40rem;
        padding: 1rem;
        padding-bottom: 3rem;
        border-radius: 12px 12px 0 0;
        position: relative;
        transition: all 0.5s ease-out;
    }
    &__principal-box-activo {
        margin-top: 0;
        width: 60rem;
        @include m.tablet {
            width: 40%;
        }
    }
    &__fotoperfil-borde,
    &__fotoperfil-borde-activo {
        margin: -17rem auto 0 auto;
        width: 34rem;
        height: 34rem;
        @include m.tablet {
            width: 34rem;
            height: 34rem;
        }
        border: 2rem solid lighten(v.$negro, 3);
        border-radius: 100%;
        transform: scale(1);
        transition: all 0.5s ease-out;
    }
    &__fotoperfil-borde-activo {
        display: none;
    }
    &__fotoperfil-principal{
        background-color: v.$secundario;
        background-image: url("../img/foto-perfil.webp");
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center -1rem;
        width: 30rem;
        height: 30rem;
        @include m.tablet {
            width: 30rem;
            height: 30rem;
        }
        border: 2px solid v.$letrasTitulo;
        border-radius: 100%;
        transform: scale(1);
        transition: all 0.5s ease-out;
    }
    &__fotoperfil-principal-activo {
        display: none;
    }
    &__titulo-bienvenida,
    &__titulo-bienvenida-activo {
        width: 90%;
        margin: 0 auto;
        padding: 1rem 0;
        transition: all 0.5s ease-out;
    }
    &__titulo-bienvenida-activo {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        gap: 1rem;
    }
    &__nombre-h2,
    &__nombre-h2-activo {
        text-align: center;
        font-size: 2.8rem;
        color: v.$letrasDos;
        text-transform: capitalize;
        margin: 0 auto 2rem auto;
        transition: all 0.5s ease-out;
    }
    &__nombre-h2-activo {
        margin: 0 auto;
    }
    &__ocupacion,
    &__ocupacion-activo  {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        text-transform: capitalize;
        color: v.$secundario;
        transition: all 0.5s ease-out;
    }
    &__navegacion,
    &__navegacion-activo {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 40%;
        margin: 1rem auto 1rem auto;
        padding: 0.6rem;
        border-radius: 0.5rem;
        background-color: lighten(v.$primario, 1);
        transform: scale(1);
        transition: all 0.5s ease-out;
    }
    &__link-bienvenida {
        text-decoration: none;
    }
    &__redsocial-box {
        padding: 0.6rem;
        border-radius: 100%;
    }
    &__redsocial-img {
        width: 2rem;
        height: 2rem;
    }
    &__contacto,
    &__contacto-activo {
        margin: 1rem auto;
        padding: 1rem;
        width: 60%;
        display: block;
        background-color: v.$letrasTitulo;
        color: lighten(v.$primario, 1);
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 900;
        text-align: center;
        transition-property: color backgroundcolor;
        transition-duration: .15s;
        &:active {
            color: v.$letrasTitulo;
            background-color: lighten(v.$primario, 1);
        }
        &:hover {
            cursor: pointer;
        }
        transition: all 0.5s ease-out;
    }
    &__contacto-activo {
        width: 60%;
        transition: all 0.5s ease-out;
        background-color: red;
    }
    &__formulario {
        display: none;
    }
    &__formulario-activo {
        margin: 0 auto;
        width: 90%;
        padding-top: 2rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        transform: scale(1);
        transition: all 0.5s ease-out;
    }
    &__boxForm {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-around;
        gap: 2rem;
    }
    &__inputForm,
    &__textareaForm {
        background-color: lighten(v.$primario, 10);
        color: v.$secundario;
        width: 100%;
        height: 3rem;
        border: none;
        padding: .5rem 1rem;
    }
    &__textareaForm {
        height: 20rem;
        min-height: 20rem;
        max-height: 20rem;
        min-width: 100%;
        max-width: 100%;
    }
}