@use 'variables' as v; 
@use 'mixins' as m; 

.text-center {
    text-align: center;
}

.bg-primario {
    background-color: v.$primario;
}

.pad {
    padding: 3rem 0 2rem 0;
}

[class*="__header"] {
    margin: 4rem 0 4rem 0;
}

[class*="__titulo-pri"] {
    color: v.$blanco;
    text-transform: capitalize;
    line-height: 0.8;
    color: v.$blanco;
    font-weight: 400;
}
[class*="__span-tit"] {
    color: v.$negro;
    font-weight: 700;
}
[class*="__span-min"] {
    color: v.$primario;
    display: block;
    font-size: 2rem;
    font-weight: 700;
}

.non {
    display: none;
}

.espaciado {
    padding: 4.5rem;
}

.emergente {
    display: inline-block;
    position: fixed;
    z-index: 1;
    top: 6rem;
    right: 4rem;
    color: darken(v.$secundario, 30);
    margin: 0.3rem;
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
        margin: 0 auto;
        background-color: v.$primario;
        padding: 0.5rem 2rem;
        width: 20rem;
        max-width: 20rem;
        border-top-right-radius: 0.6rem;
        border-top-left-radius: 0.6rem;
    }
    &__h3 {
        margin: 0;
        font-size: 2rem;
    }
    &__info {
        margin: 0;
        width: 20rem;
        max-width: 20rem;
        background-color: lighten(v.$primario, 6);
        border-bottom-right-radius: 0.6rem;
        border-bottom-left-radius: 0.6rem;
    }
    &__texto {
        color: v.$fondo;
        font-size: 1.4rem;
        padding: 0.5rem;
        text-align: center;
    }
}


.icono--error,
.icono--done {
    font-size: 20px;
    font-weight: 400;
    margin: 0;
}
.icono--error {
    color: red;
}
.error {
    color: darken(v.$secundario, 30);
}
.icono--done {
    color: v.$verde;
}
.done {
    color: v.$verde;
}

.scale0 {
    transform: scale(0);
    transition: transform 0.3s ease-in-out;
}
.scale1 {
    transform: scale(1);
    transition: transform 0.3s ease-in-out;
}